}

.country-contacts-container {
  max-width: 1200px;
  width: 100%;
  height: auto;
  margin: 0 auto;
  position: relative;
}

.country-contacts-container:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #fff5f6;
  border-radius: 5% 5% 0 0;
  margin-top: 50px;
  position: absolute;
  top: 0;
  left: 0;
}

.country-contacts-container:after {
  content: "";
  width: 100%;
  height: 50px;
  z-index: -1;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
}

.list-country-contacts {
  flex-wrap: wrap;
  justify-content: center;
  gap: 32px;
  list-style-type: none;
  display: flex;
}

.contacts-item-container {
  height: 332px;
  width: 216px;
  box-sizing: content-box;
  padding: 40px;
}

@media screen and (min-width: 768px) {
  .contacts-item-container {
    width: 260px;
    height: 528px;
    box-sizing: border-box;
  }
}

@media screen and (min-width: 1200px) {
  .contacts-item-container {
    width: 288px;
    height: 448px;
    box-sizing: border-box;
  }
}

.contacts-separator {
  max-width: 100%;
}

.list-country-contacts {
  padding: 0;
}

.list-country-contacts li {
  background-color: #fff;
  border-radius: 24px;
  padding: 0;
  box-shadow: 0 8px 30px rgba(212, 20, 67, .1);
}

@media screen and (min-width: 768px) {
  .list-country-contacts li {
    width: 295px;
    height: 528px;
  }
}

@media screen and (min-width: 1200px) {
  .list-country-contacts li {
    width: 265px;
    height: 538px;
    padding: 40px;
  }
}

.orange-contacts-title {
  background: var(--background-section3-section7-bej);
  color: var(--font-color-white);
  text-align: center;
  width: 25%;
  border-radius: 6px;
  padding: 5px 0;
}

.green-contacts-title {
  background: var(--background-section3-section7-verde);
  color: var(--font-color-white);
  text-align: center;
  width: 45%;
  border-radius: 6px;
  padding: 5px 0;
}

.city-description-container, .timetable-container, .contacts-info-container {
  margin-top: 32px;
}

.contacts-description {
  color: #907e82;
}

.contacts-titles {
  color: #000;
}

.contacts-timetable {
  color: var(--background-section2-roz-inchis);
}

.contacts {
  color: #907e82;
  text-decoration: none;
  display: block;
}

.contacts-buttons {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px;
  display: flex;
}

.contacts-buttons .button {
  width: 168px;
  height: 44px;
  color: var(--background-buttonRed-section2-section7);
  text-align: center;
  background: #fff;
  border: none;
  border-radius: 22px;
  align-items: center;
  font-weight: 700;
}

.button:hover, .button:focus {
  background: var(--background-buttonRed-section2-section7);
  color: var(--font-color-white);
  border: solid var(--font-color-white);
}

.arrow {
  border: solid var(--background-buttonRed-section2-section7);
  border-width: 0 3px 3px 0;
  padding: 3px;
  display: inline-block;
  transform: rotate(-45deg);
}

.arrow:hover, .arrow:focus {
  border: solid var(--font-color-white);
  border-width: 0 3px 3px 0;
  padding: 3px;
  display: inline-block;
}

.is-hidden {
  visibility: hidden;
  transform: scale(1.5);
}

.modal {
  width: 100vw;
  height: 100vh;
  transition: transform var(--transition);
  background: #fff;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  background-color: var(--background-modal);
  width: 408px;
  height: 584px;
  border-radius: 4px;
  flex-direction: column;
  align-items: center;
  padding: 24px;
  display: flex;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 1px 2px rgba(0, 0, 0, .2);
}

.close-btn {
  width: 24px;
  height: 24px;
  color: #907e82;
  cursor: pointer;
  background-color: #fff;
  border-style: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin-bottom: 24px;
