  letter-spacing: .04em;
  text-align: center;
  color: #fff;
  cursor: pointer;
  background: #907e82;
  border-style: none;
  border-radius: 4px;
  padding: 16px 32px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.modal-send-btn:hover {
